#todo-list {
  padding: 0 20px;
  .btn {
    cursor: pointer;
    background-color: green;
    width: 50px;
    height: 50px;
  }

  .container {
    margin: 30px 20px;
    background-color: #ffffff;
    padding: 30px;
    border-radius: 4px;
    box-shadow: 5px 10px 9px #2a2a5154;
    transition: height 350ms ease-in, margin-top 350ms ease-in;

    input { outline: none; }

    #error {
      color: red;
      font-weight: bold;
      text-align: center;
      height: 0;
      overflow: hidden;
      opacity: 0;
      transition: height 350ms ease-in, margin-top 350ms ease-in, opacity 250ms ease-in;
      &.show {
        margin-top: 20px;
        height: 20px;
        opacity: 1;
      }
    }

    #todo {
      display: flex;
      input[type='text'] {
        display: inline-block;
        width: 100%;
        margin-right: 10px;
        padding: 12px;
        outline: 0;
        /* float: left; */
        border-radius: 2px;
        border: 2px solid #d6d6e4;
        -webkit-box-shadow: inset 0 0 0 0 transparent;
        box-shadow: inset 0 0 0 0 transparent;
        color: #767676;
        font-weight: 700;
        transition: border-color 250ms ease-in;
        &:hover {
          border-color: #a5a5bb;
        }
      }

      button {
        height: 40px;
        background: #36495d;
        width: 60px;
        display: inline-block;
        float: right;
        margin-left: 1px;
        color:#fff;
        border-radius: 2px;
        cursor: pointer;
        opacity: 0.8;
        transition: opacity 250ms ease-in;

        &.add {
          opacity: 1;
        }
        &:hover {
          opacity: 1;
        }
      }
      button:focus {outline:0;}
    }
    ul {
      margin: 0;
      li {
        margin-top: 20px;
        list-style: none;

        &.checked {
          .check-box-title input {
            text-decoration: line-through;
            text-decoration-color: red;
          }
          input[type='checkbox'] ~ label {
            border: 0px solid white;
            background: #47b3b6;
            box-shadow: 0 0 0 2px #47b3b6;
            transition: background 250ms ease-in;
            &:before {
              content: '\2713';
              width: 5px;
              height: 5px;
              color: #fff;
              left: -5px;
              right: 0;
              top: -1px;
              margin: 0 auto;
              font-size: 14px;
              position: absolute;
              margin: 0 auto;
            }
          }
        }

        .num {
          display: inline-block;
          position: relative;
          top: -2px;
        }

        input[type='checkbox'] {
          display: none;
        }

        .check_box {
          position: relative;
          top: 3px;
          cursor: pointer;
          padding-left: 10px;
        }

        .check_box label {
          position: relative;
          box-sizing: border-box;
          cursor: pointer;
          border: 0px solid transparent;
          border-radius: 2px;
          width: 20px;
          height: 20px;
          transition: all 0.1s ease;
          z-index: 2;
          box-shadow: 0px 0px 0px 2px #d6d6e4;
          display: block;
          pointer-events: none;
        }

        .check-box-title {
          padding-left: 20px;
          position: relative;
          top: -3px;
          input {
            border: 0;
            font-weight: 700;
            color: #5e6c7d;
          }
        }

        .check-box-close {
          line-height: 14px;
          color: silver;
          font-weight: bolder;
          font-size: 19px;
          float: right;
          top: 7px;
          position: relative;
          font-family: cursive;
          cursor: pointer;
          &:before {
            content: '\2718';
            color: #e2ccd4;
            transition: color 250ms ease-in, scale 200ms ease-in;
          }
          &:hover {
            &:before {
              color: #E91E63;

            }
          }
        }

        .check_box,
        .check-box-title,
        .check-box-close {
          display: inline-block;
        }
      }
    }
  }
}
